  border-radius: 50%;
  content: "";
  height: 6px;
  left: -2.5px;
  position: absolute;
  top: calc(9px + 12px - 3px);
  top: calc(9px + 1.2rem - 3px);
  width: 6px;
}
aio-toc .toc-inner ul.toc-list:not(.embedded) li:before {
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  top: 0;
  border-left-width: 1px;
  border-left-style: solid;
}
